import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

// extracted component receiving quote text as props,
// see QuoteBox render() to use it instead of <div>
// function Text(props) {
//     return (
//         <div id="text"><em>{props.text}</em></div>
//     )
// }

class QuoteBox extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            quote: {text: '', author: ''},
            color: ''
        }
        this.fetchQuote = this.fetchQuote.bind(this);
    }

    componentDidMount() {
        return this.fetchQuote();
    }

    changeColor() {
        const cssColors = ["brown", "chocolate", "coral", "crimson", "darkcyan", "darkolivegreen", "darkred", "midnightblue", "deeppink", "green", "seagreen", "steelblue", "indigo", "darkslateblue", "slategray", "orangered", "mediumpurple", "mediumorchid", "cornflowerblue", "sienna", "olive", "teal", "purple", "darkslategrey", "maroon"];
        return cssColors[Math.floor(Math.random() * 25)];  
    }

    fetchQuote() {
        const url = "https://talaikis.com/api/quotes/random/";
        const color = this.changeColor();
        fetch(url).then(data => data.json())
        .then(data => {
            this.setState({
                quote: {
                    text: data.quote,
                    author: data.author
                },
                color: color
            })
        });
    }
    render() {
        const tweetThis = "https://twitter.com/intent/tweet?text="
                        + this.state.quote.text + " " + this.state.quote.author;
        document.querySelector("body").style.backgroundColor = this.state.color;

        return (
            <div id="quote-box" style={{color: this.state.color}}>
                <div id="text"><em>{this.state.quote.text}</em></div>
                {/* same as div above, for demo,
                  if using extracted component at the top of file */}
                {/* <Text text={this.state.quote.text} />  */}
               
                <div id="author">- {this.state.quote.author}</div>
                <button id="new-quote" onClick={this.fetchQuote} style={{backgroundColor: this.state.color}}>new quote</button>
                <a href={tweetThis}>
                    <button id="tweet-quote" style={{backgroundColor: this.state.color}}>tweet</button>
                </a>
            </div>
        )
    }
}

ReactDOM.render(<QuoteBox />, document.getElementById('root'));



